import React from 'react';
import { Button } from '../Button/Button';
import { CustomInput } from '../CustomInput/CustomInput';
import { ModalSuccess } from '../ModalSuccess/ModalSuccess';
import { useBoolean } from '../../hooks';

import './Form.style.scss';

export const Form = ({ closeModalOrder }) => {
  const [modalSuccessIsOpen, setSuccessIsOpen] = useBoolean(false);

  const submitHadler = () => {
    setSuccessIsOpen.on();
  };

  return (
    <div className="form">
      <h2 className="form_title"> &nbsp;</h2>
      <p className="form_subtitle">&nbsp; </p>
      <form action="#" method="post">
        <CustomInput
          type="text"
          name="name"
          placeholder=""
          className="customInput--name"
        />
        <CustomInput
          type="text"
          name="login"
          placeholder="   "
          className="customInput--login"
        />
      </form>
      <p className="form_text">
        &nbsp; &nbsp; &nbsp;Telegram &nbsp; 
        .
      </p>
      <Button onClick={submitHadler} text=" " type="button" />
      <ModalSuccess
        modalIsOpen={modalSuccessIsOpen}
        closeModal={setSuccessIsOpen.off}
        closeModalOrder={closeModalOrder}
      />
    </div>
  );
};
